//自定义tabbar 
export default {
	props: {
		tabs: {
			type: Array
		},
		current: {
			type: String
		}
	},
	data() {
		return {
			activeindex: 0,
		}
	},
	onLoad() {
		this.activeindex = this.current;
	},

	methods: {
		jump(page, url, index) {
			console.log('跳转路径', url, index, this.current);
			if (url == 'user' || url == 'index') {
				uni.reLaunch({
					url: `/pages/${page}/${url}`
				});
				return
			}
			console.log(url)
			uni.redirectTo({
				url: `./${url}`
			});
		}
	}
}
